<template>

	<v-layout row wrap>
		<v-flex v-if="isFiltered(getSeachWord(project))" tag="article" xs12 md6 lg4 v-for="project in projects" :key="project.id">
			<v-card class="project-card" style="height: 100%" :href="project.html_url" hover flat tile>
				<v-card-title class="project-card-title">
					<v-icon class="pr-2">mdi-github-face</v-icon>
					<span>{{project.full_name}}</span>
				</v-card-title>
				<v-card-text class="project-card-content">
					{{project.description}}
				</v-card-text>
				<v-card-text class="project-card-footer">
					<v-layout>
						<v-flex shrink class="project-card-footer-info">
							<v-layout row wrap align-center>
								<v-flex>
									<v-icon>mdi-star</v-icon>
								</v-flex>
								<v-flex shrink>
									<span>{{project.stargazers_count}}</span>
								</v-flex>
							</v-layout>
						</v-flex>
						<v-flex v-if="project.homepage" shrink class="project-card-footer-info">
							<v-layout row wrap align-center>
								<v-flex>
									<v-icon>mdi-home</v-icon>
								</v-flex>
								<v-flex shrink>
									<a :href="project.homepage">Homepage</a>
								</v-flex>
							</v-layout>
						</v-flex>
						<v-flex v-if="project.language" shrink class="project-card-footer-info">
							<v-layout row wrap align-center>
								<v-flex>
									<v-icon>mdi-code-tags</v-icon>
								</v-flex>
								<v-flex shrink>
									{{project.language}}
								</v-flex>
							</v-layout>
						</v-flex>
						<v-flex v-if="project.license" shrink class="project-card-footer-info">
							<v-layout row wrap align-center>
								<v-flex>
									<v-icon>mdi-scale-balance</v-icon>
								</v-flex>
								<v-flex shrink>
									{{project.license.name}}
								</v-flex>
							</v-layout>
						</v-flex>
					</v-layout>
				</v-card-text>
			</v-card>
		</v-flex>
	</v-layout>
</template>

<script>
	import {mapState} from "vuex";

	export default {
		name: "GithubStars",
		data: function () {
			return {}
		},
		mounted()
		{
			this.$store.dispatch('loadGithubStars');
		},
		computed: mapState([
			'projects',
			'search',
		]),
		methods: {
			getSeachWord(project){
				let words = [];

				words.push(project.name);
				words.push(project.owner.login);
				if (project.language)
					words.push(project.language);

				if (project.description)
				{
					words = words.concat(project.description.match(/\b(\w+)\b/g));
				}

				return words.map(function(word){
					return word.toLowerCase();
				}).join(' ');
			},
			isFiltered(words){
				if (this.search && typeof this.search == 'string' && this.search.length >= 3)
				{
					return words.indexOf(this.search) > -1;
				}

				return true;
			},
		}
	}
</script>

<style scoped lang="scss">
	.project-card {
		display: flex;
		flex-flow: column nowrap;

		.project-card-title {
			color: var(--v-primary-base) !important;
			flex: 0 0 auto;
		}

		.project-card-content{
			flex: 1 0 auto;
		}

		.project-card-footer {
			flex: 0 0 auto;
			.project-card-footer-info {
				margin-right: 1em;

				a:last-child{
					margin-right: 0;
				}
				.flex {
					padding: 0;

					.v-icon {
						padding-right: 0.2em;
					}

					*{
						color: var(--v-secondary-lighten5) !important;
						text-decoration: none !important;
					}
				}
			}
		}

	}
</style>