<template>
	<v-app>
		<v-overlay opacity="1" :value="loader">
			<v-progress-circular indeterminate size="64"></v-progress-circular>
		</v-overlay>
		<v-app-bar
			app
			clipped-left
		>
			<v-text-field
				@input="updateSearch"
				:value="search"
				solo-inverted
				flat
				hide-details
				label="Search"
				prepend-inner-icon="mdi-magnify"
			></v-text-field>
		</v-app-bar>

		<v-content>
			<v-container
				fluid
				grid-list-lg
				tag="main"
			>
				<router-view></router-view>
			</v-container>
		</v-content>
	</v-app>
</template>

<script>
	import {mapState} from "vuex";

	export default {
		props: {
			source: String,
		},
		created()
		{
			this.$vuetify.theme.dark = true
		},
		computed: mapState([
			'loader',
			'search'
		]),
		methods: {
			updateSearch(value){
				this.$store.commit('setSearch', value)
			}
		}
	}
</script>